  background-size: auto;
  /* background-image: url(./background.jpg); */
  background-position: top;
  border-radius: 10px;
  width: max-content;
  min-width: 500px;
}

.link {
  background-color: rgb(0, 13, 20);
  text-decoration: none;
  color: rgba(192, 247, 1);
  border-radius: 5px;
}

@media (max-width: 500px) {
  .container {
    min-width: 100dvw;
    width: 100%;
    grid-template-columns: auto;
    grid-template-rows: auto;
  }
}

.clearButton {
  font-size: 14px;
  background-color: rgba(192, 247, 1);
  padding: 15px;
  border: 1px black solid;
  width: 160px;
  height: 64px;
  border-radius: 50px;
  cursor: pointer;
}
